body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Open Sans', sans-serif;
  padding: 10px;
}

.App a {
  text-decoration: none;
  font-weight: 900;
  color: #F346A4;
}

.App h1 {
  text-transform: uppercase;
  text-align: center;
  font-size: 40px;
  line-height: 1;
  margin-top: 0;
}

.logo {
  max-width: 100%;
  display: block;
  margin: 30px auto 40px;
}

.sailor-sprites {
  list-style-type: none;
  padding-left: 0;
  max-width: 600px;
  margin: 0 auto 10px;
  text-align: center;
}

.sailor-sprites .sailor-sprite {
  display: inline-block;
}

.byline {
  text-align: center;
  font-size: 12px;
}
.media-item {
  max-width: 600px;
  margin: 0 auto 40px;
}

.media-item h2 {
  margin-top: 0;
  margin-bottom: 0;
}

.media-item-type {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
}

.star-list {
  list-style-type: none;
  padding-left: 0;
  margin-left: -5px;
  margin-right: -5px;
  display: flex;
  flex-wrap: wrap;
}

.star-list.movie {
  display: block;
}

.star-list-item {
  margin-left: 5px;
  margin-right: 5px;
  position: relative;
}

.star-list-item-movie {
  background-color: #DDD;
  border-radius: 10px;
  padding: 10px;
  font-size: 12px;
  font-weight: 800;
}

.star-list-item-movie.done {
  background-color: #000;
  color: #FFF;
}

.star-list-item .star-number {
  position: absolute;
  font-weight: 900;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.filler {
  background-image: -webkit-gradient(linear, left top, right top, from(#488fba), color-stop(#00a5c6), color-stop(#00babd), color-stop(#00cba0), color-stop(#6cd876), color-stop(#a2d14e), color-stop(#d2c52f), color-stop(#ffb42c), color-stop(#ff9141), color-stop(#ff6e60), color-stop(#ff5083), to(#f443a6));
  background-image: -webkit-linear-gradient(left, #488fba, #00a5c6, #00babd, #00cba0, #6cd876, #a2d14e, #d2c52f, #ffb42c, #ff9141, #ff6e60, #ff5083, #f443a6);
  background-image: linear-gradient(to right, #488fba, #00a5c6, #00babd, #00cba0, #6cd876, #a2d14e, #d2c52f, #ffb42c, #ff9141, #ff6e60, #ff5083, #f443a6);
  height: 100%;
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  -webkit-transition: width 0.2s ease-in;
  transition: width 0.2s ease-in;
  position: relative;
}

.filler .percentage {
  position: absolute;
  top: 6px;
  right: -22px;
  font-weight: 900;
  font-size: 12px;
}
.progress-bar {
  position: relative;
  height: 25px;
  max-width: 600px;
  margin: 0 auto;
  border-radius: 50px;
  border: 2px solid #000;
  margin-bottom: 30px;
}
